@import url('https://fonts.googleapis.com/css?family=Satisfy');

.ProfilePage_cardStyle {
    padding: 20px;
    margin: 60px 0;
    text-align: center;
    border-radius: 0.5rem;
    background-color: white;
    min-height: 80%;
}

.ProfilePage_imgStyle {
    background-repeat: no-repeat;
    background-position: 50%;
    border-radius: 50%;
    width: 200px;
    height: 200px;
    position: relative;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 25px;
    border: 2px solid #d8d8d8;
}

.ProfilePage_userName {
    font-family: 'Satisfy', cursive;
}

/* BUTTON STYLES */
.ProfilePage_updateButtonStyle {
    font-weight: bold;
    color: rgb(236, 221, 210);
    border: none;
    background-color: #785964;
    margin: 20px 0;
    width: 50%;
    height: 50%;
    min-width: 100px;
    font-size: 1rem;
    position: relative;
    text-align: center;
}

.ProfilePage_updateButtonStyle:hover {
    background-color: #917881;
}

.ProfilePage_updateButtonStyle:active {
    background-color: #785964;
}

.ProfilePage_userDataInfoButtons {
    font-weight: bold;
    color: rgb(236, 221, 210);
    border: none;
    background-color: #785964;
    min-width: 100px;
    font-size: 1rem;
    margin-left: 5px;
    margin-bottom: 10px;
}

.ProfilePage_formInputElement {
    margin-bottom: 20px;
}


